import React from "react";
import { connect } from "react-redux";
import {
  setCurrentPage,
  toggleIsFollowing,
  requestUsers,
  follow,
  unfollow,
} from "../../redux/users-reducer";
import Users from "./Users";
import Preloader from "../common/preloader/Preloader";
import { withAuthRedirect } from "../../hoc/withAuthRedirect";
import { compose } from "redux";
import { getUsers, getPageSize, getTotalItemsCount, getPortionSize, getCurrentPage, getIsFetching, getFollowingInProgress, getportionCount } from "../../redux/users-selectors";

class UsersComponent extends React.Component {
  componentDidMount() {
    this.props.requestUsers(this.props.currentPage, this.props.pageSize);
  }

  onSetCurrentPage = (pageNumber) => {
    this.props.setCurrentPage(pageNumber);

    this.props.requestUsers(pageNumber, this.props.pageSize);
  };

  render() {
    return (
      <>
        {this.props.isFetching ? <Preloader /> : null}
        <Users
          currentPage={this.props.currentPage}
          onSetCurrentPage={this.onSetCurrentPage}
          totalItemsCount={this.props.totalItemsCount}
          portionSize={this.props.portionSize}
          pageSize={this.props.pageSize}
          users={this.props.users}
          follow={this.props.follow}
          unfollow={this.props.unfollow}
          followingInProgress={this.props.followingInProgress}
        />
      </>
    );
  }
}

let mapStateToProps = (state) => {
  return {
    users: getUsers(state),
    pageSize: getPageSize(state),
    totalItemsCount: getTotalItemsCount(state),
    portionSize: getPortionSize(state),
    currentPage: getCurrentPage(state),
    isFetching: getIsFetching(state),
    followingInProgress: getFollowingInProgress(state),
  };
};

export default compose(
  connect(mapStateToProps, { setCurrentPage, requestUsers, follow, unfollow }),
  withAuthRedirect
)(UsersComponent);
